import { ChangeDetectorRef, Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { AvatarStyle, ListItemStyle } from '@cometchat/uikit-elements';
import { UserMemberListType, UserPresencePlacement } from '@cometchat/uikit-resources';
import { CometChatThemeService } from '../CometChatTheme.service';



@Component({
  selector: 'cometchat-user-member-wrapper',
  templateUrl: './cometchat-user-member-wrapper.component.html',
  styleUrls: ['./cometchat-user-member-wrapper.component.scss']
})


export class CometChatUserMemberWrapperComponent {
  @Input() userMemberListType!: UserMemberListType;
  @Input() onItemClick!: (userMember: CometChat.User | CometChat.GroupMember) => void;
  @Input() listItemView: any;
  @Input() avatarStyle!: AvatarStyle;
  @Input() statusIndicatorStyle: any;
  @Input() searchKeyword!: string;
  @Input() group!: CometChat.Group;
  @Input() subtitleView?: any;
  @Input() usersRequestBuilder!: CometChat.UsersRequestBuilder;
  @Input() disableUsersPresence!: boolean;
  @Input() userPresencePlacement!: UserPresencePlacement;
  @Input() hideSeperator!: boolean;
  @Input() loadingStateView: any;
  @Input() onEmpty?: () => void;
  @Input() onError?: (error: CometChat.CometChatException) => void;
  @Input() groupMemberRequestBuilder!: CometChat.GroupMembersRequestBuilder;
  @Input() loadingIconUrl! : string;
  @Input() disableLoadingState!: boolean;
  public userMemberListTypeEnum =  UserMemberListType;

  constructor(
    private themeService: CometChatThemeService
  ) { }


  public listItemStyle = new ListItemStyle({
    height: 'fit-content'
  })

   getUsersStyle = () => {
    return{
      border: '1px solid ' + this.themeService.theme.palette.getAccent300(),
      background: this.themeService.theme.palette.getBackground(),
      borderRadius: '12px 12px 12px 12px'
    };
  };

   getGroupMemebersStyle = () => {
    return {
      border: '1px solid ' + this.themeService.theme.palette.getAccent300(),
      padding: "0px",
      background: this.themeService.theme.palette.getBackground(),
      borderRadius: '12px 12px 12px 12px',
    };
  };

}
