  /**
   * @param  {CometChat.Conversation} conversation
   */
  checkStatusType(conversation: CometChat.Conversation) {
    let item: CometChat.User | CometChat.Group = conversation.getConversationWith()
    if (
      item instanceof CometChat.User
    ) {
      let userStatusVisibility = new MessageUtils().getUserStatusVisibility(item) || this.disableUsersPresence;
      if (!userStatusVisibility)
        return this.statusColor[item?.getStatus()];
      else return null;
    }
    else {
      return this.statusColor[item?.getType()]
    }
  }

  getExtensionData(messageObject: CometChat.BaseMessage) {
    let messageText;
    //xss extensions data
    const xssData = CometChatUIKitUtility.checkMessageForExtensionsData(
      messageObject,
      "xss-filter"
    );
    if (
      xssData &&
      CometChatUIKitUtility.checkHasOwnProperty(xssData, "sanitized_text") &&
      CometChatUIKitUtility.checkHasOwnProperty(xssData, "hasXSS") &&
      xssData.hasXSS === "yes"
    ) {
      messageText = xssData.sanitized_text;
    }
    //datamasking extensions data
    const maskedData = CometChatUIKitUtility.checkMessageForExtensionsData(
      messageObject,
      "data-masking"
    );
    if (
      maskedData &&
      CometChatUIKitUtility.checkHasOwnProperty(maskedData, "data") &&
      CometChatUIKitUtility.checkHasOwnProperty(
        maskedData.data,
        "sensitive_data"
      ) &&
      CometChatUIKitUtility.checkHasOwnProperty(
        maskedData.data,
        "message_masked"
      ) &&
      maskedData.data.sensitive_data === "yes"
    ) {
      messageText = maskedData.data.message_masked;
    }
    //profanity extensions data
    const profaneData = CometChatUIKitUtility.checkMessageForExtensionsData(
      messageObject,
      "profanity-filter"
    );
    if (
      profaneData &&
      CometChatUIKitUtility.checkHasOwnProperty(profaneData, "profanity") &&
      CometChatUIKitUtility.checkHasOwnProperty(profaneData, "message_clean") &&
      profaneData.profanity === "yes"
    ) {
      messageText = profaneData.message_clean;
    }
    return messageText || (messageObject as any).text;
  }
  setSubtitle = (conversationObject: CometChat.Conversation) => {
    if (this.typingIndicator) {
      const isTyping =
        (conversationObject as any)?.conversationWith?.guid ==
        this.typingIndicator.getReceiverId();
      if (isTyping) {
        return `${this.typingIndicator.getSender().getName()} ${this.typingIndicatorText
          }`;
      } else if (
        (conversationObject as any)?.conversationWith?.uid ==
        this.typingIndicator?.getSender().getUid() &&
        this.typingIndicator.getReceiverType() !==
        CometChatUIKitConstants.MessageReceiverType.group
      ) {
        return this.typingIndicatorText;
      }
    }
    let subtitle = ChatConfigurator.getDataSource().getLastConversationMessage(
      conversationObject,
      this.loggedInUser!,

      {
        disableMentions: this.disableMentions,
        theme: this.themeService.theme,
        mentionsTargetElement: MentionsTargetElement.conversation,
        textFormatters: this.textFormatters
      }
    );
    let icon =
      conversationObject?.getLastMessage()?.getType() ==
        CometChatUIKitConstants.MessageTypes.audio
        ? " "
        : " ";

    return this.sanitizer.bypassSecurityTrustHtml(
      conversationObject?.getLastMessage()?.getCategory() ==
        CometChatUIKitConstants.MessageCategory.call
        ? icon + subtitle
        : subtitle
    );
  };

