  checkGroupType(conversation: CometChat.Conversation): string {
    let image: string = "";
    if (
      conversation.getConversationType() ==
      CometChatUIKitConstants.MessageReceiverType.group
    ) {
      let group: CometChat.Group = conversation.getConversationWith() as CometChat.Group;
      switch (group.getType()) {
        case CometChatUIKitConstants.GroupTypes.password:
          image = this.passwordGroupIcon || this.protectedGroupIcon;
          break;
        case CometChatUIKitConstants.GroupTypes.private:
          image = this.privateGroupIcon;
          break;
        default:
          image = "";
          break;
      }
    }
    return image;
  }
