  isAudioOrVideoMessage(message: CometChat.BaseMessage) {
    const messageType = message?.getType();
    const typesToCheck = [
      CometChatUIKitConstants.MessageTypes.image,
      CometChatUIKitConstants.MessageTypes.video,
    ];
    return typesToCheck.includes(messageType);
  }

  setBubbleAlignment = (message: CometChat.BaseMessage) => {
    let alignment: MessageBubbleAlignment = MessageBubbleAlignment.center;
    if (this.alignment == MessageListAlignment.left) {
      alignment = MessageBubbleAlignment.left;
    } else {
      if (
        message?.getType() ==
        CometChatUIKitConstants.MessageTypes.groupMember ||
        message.getCategory() == this.callConstant
      ) {
        alignment = MessageBubbleAlignment.center;
      } else if (
        !message?.getSender() ||
        (message?.getSender().getUid() == this.loggedInUser.getUid() &&
          message?.getType() !=
          CometChatUIKitConstants.MessageTypes.groupMember)
      ) {
        alignment = MessageBubbleAlignment.right;
      } else {
        alignment = MessageBubbleAlignment.left;
      }
    }
    return alignment;
  };

