  getTextMessage(message: CometChat.TextMessage): string {
    var text = this.enableDataMasking
      ? CometChatUIKitUtility.getExtensionData(message)
      : null;
    return text?.trim()?.length > 0 ? text : message.getText();
  }
  getLinkPreview(message: CometChat.TextMessage): any {
    try {
      if (message?.getMetadata() && this.enableLinkPreview) {
        var metadata: any = message.getMetadata();
        var injectedObject = metadata[LinkPreviewConstants.injected];
        if (injectedObject && injectedObject?.extensions) {
          var extensionsObject = injectedObject.extensions;
          if (
            extensionsObject &&
            CometChatUIKitUtility.checkHasOwnProperty(
              extensionsObject,
              LinkPreviewConstants.link_preview
            )
          ) {
            var linkPreviewObject =
              extensionsObject[LinkPreviewConstants.link_preview];
            if (
              linkPreviewObject &&
              CometChatUIKitUtility.checkHasOwnProperty(
                linkPreviewObject,
                LinkPreviewConstants.links
              ) &&
              linkPreviewObject[LinkPreviewConstants.links].length
            ) {
              return linkPreviewObject[LinkPreviewConstants.links][0];
            } else {
              return null;
            }
          } else {
            return null;
          }
        }
      } else {
        return null;
      }
    } catch (error: any) {
      if (this.onError) {
        this.onError(CometChatException(error));
      }
    }
  }
  getImageThumbnail(msg: CometChat.MediaMessage): string {
    var message: any = msg as CometChat.MediaMessage;
    let imageURL = "";
    if (this.enableThumbnailGeneration) {
      try {
        var metadata: any = message.getMetadata();
        var injectedObject = metadata?.[
          ThumbnailGenerationConstants.injected
        ] as { extensions?: any };
        var extensionsObject = injectedObject?.extensions;
        var thumbnailGenerationObject =
          extensionsObject[ThumbnailGenerationConstants.thumbnail_generation];
        var imageToDownload = thumbnailGenerationObject?.url_small;
        if (imageToDownload) {
          imageURL = imageToDownload;
        } else {
          imageURL = message?.data?.attachments
            ? message?.data?.attachments[0]?.url
            : "";
        }
      } catch (error: any) {
        if (this.onError) {
          this.onError(CometChatException(error));
        }
      }
    } else {
      imageURL = message?.data?.attachments
        ? message?.data?.attachments[0]?.url
        : "";
    }
    return imageURL;
  }
  getLinkPreviewDetails(key: string, message: CometChat.TextMessage): string {
    let linkPreviewObject: any = this.getLinkPreview(message);
    if (Object.keys(linkPreviewObject).length > 0) {
      return linkPreviewObject[key];
    } else {
      return "";
    }
  }
