import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnChanges, OnInit, SimpleChanges, TemplateRef } from '@angular/core';
import { CometChatMessageEvents, CometChatMessageTemplate, CometChatUIKitConstants, DatePatterns, Receipts, States, fontHelper, localize } from '@cometchat/uikit-resources';
import { DateStyle, LabelStyle, ListItemStyle, ReceiptStyle } from '@cometchat/uikit-elements';
import { ListStyle, MessageInformationStyle, MessageReceiptUtils } from '@cometchat/uikit-shared';

import { CometChat } from '@cometchat/chat-sdk-javascript';
import { CometChatException } from '../../Shared/Utils/ComeChatException';
import { CometChatThemeService } from '../../CometChatTheme.service';
import { Subscription } from "rxjs";
import { CometChatUIKit } from '../../Shared/CometChatUIkit/CometChatUIKit';

/**
*
* CometChatMessageInformationComponent is a used to render listitem component.
*
* @version 1.0.0
* @author CometChatTeam
* @copyright  2022 CometChat Inc.
*
*/
@Component({
  selector: 'cometchat-message-information',
  templateUrl: './cometchat-message-information.component.html',
  styleUrls: ['./cometchat-message-information.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CometChatMessageInformationComponent implements OnInit, OnChanges {
  @Input() closeIconURL: string = "assets/close2x.svg";
  @Input() message!: CometChat.BaseMessage;
  @Input() title: string = localize("MESSAGE_INFORMATION");
  @Input() template!: CometChatMessageTemplate;
  @Input() bubbleView!: TemplateRef<any>;
  @Input() subtitleView!: TemplateRef<any>;
  @Input() listItemView!: TemplateRef<any>;
  @Input() receiptDatePattern!: (timestamp: number) => string;
  @Input() onError: ((error: CometChat.CometChatException) => void) | null = (error: CometChat.CometChatException) => {
    console.log(error)
  }
  @Input() messageInformationStyle: MessageInformationStyle = {
    height: "500px",
    width: "500px"
  };
  @Input() readIcon: string = "assets/message-read.svg";
  @Input() deliveredIcon: string = "assets/message-delivered.svg";
  @Input() onClose!: () => void;
  @Input() listItemStyle: ListItemStyle = {};
  @Input() emptyStateText: string = localize("NO_RECIPIENTS")
  @Input() errorStateText: string = localize("SOMETHING_WRONG");
  @Input() emptyStateView!: TemplateRef<any>;
  @Input() loadingIconURL: string = "assets/Spinner.svg";
  @Input() loadingStateView!: TemplateRef<any>;
  @Input() errorStateView!: TemplateRef<any>;
  onMessagesDelivered!: Subscription;
  onMessagesRead!: Subscription;
  receipts: CometChat.MessageReceipt[] = [];
  receiptStyle = new ReceiptStyle({
    waitIconTint: this.themeService.theme.palette.getAccent700(),
    sentIconTint: this.themeService.theme.palette.getAccent600(),
    deliveredIconTint: this.themeService.theme.palette.getAccent600(),
    readIconTint: this.themeService.theme.palette.getPrimary(),
    errorIconTint: this.themeService.theme.palette.getError(),
    height: "20px",
    width: "20px",
    background: "transparent"
  })
  isUserType: boolean = false;
  deliveredReceipt: Receipts = Receipts.delivered;
  readReceipt: Receipts = Receipts.read;
  listStyle: ListStyle = {};
  messageText: string = localize("MESSAGE");
  receiptInfoText: string = localize("RECEIPT_INFORMATION")
  emptyLabelStyle: LabelStyle = {
    textFont: '400 11px Inter',
    textColor: "black"
  }
  dateStyle: DateStyle = {

  }
  loggedInUser: CometChat.User | null = null;
  dividerStyle: any = {}
  states: States = States.loading
  constructor(private ref: ChangeDetectorRef, private themeService: CometChatThemeService) { }
  ngOnChanges(changes: SimpleChanges): void {

  }
  getDatePattern(timestamp: number) {
    if (this.receiptDatePattern) {
      return this.receiptDatePattern(timestamp)
    }
    else {
      return undefined;
    }
  }
  ngOnInit(): void {
    this.setTheme()
    CometChatUIKit.getLoggedinUser()!.then((user: CometChat.User | null) => {
      if (user) {
        this.loggedInUser = user;
        this.getMessageReceipts()
        this.addMessageEventListeners()
      }
    })


  }
  setTheme() {
    this.setMessageInfoStyle()
    this.setListItemStyle()
    this.listStyle = {
      titleTextFont: this.messageInformationStyle.titleTextFont,
      titleTextColor: this.messageInformationStyle.titleTextColor,
      emptyStateTextFont: this.messageInformationStyle.emptyStateTextFont,
      emptyStateTextColor: this.messageInformationStyle.emptyStateTextColor,
      errorStateTextFont: this.messageInformationStyle.errorStateTextFont,
      errorStateTextColor: this.messageInformationStyle.errorStateTextColor,
      loadingIconTint: this.messageInformationStyle.loadingIconTint,
    }
    this.dividerStyle = {
      background: this.messageInformationStyle.dividerTint,
      height: "1px",
      width: "100%",
    }
    this.dateStyle = {
      textFont: '400 11px Inter',
      textColor: this.themeService.theme.palette.getAccent600(),
      background: "transparent",
      height: "100%",
      width: "100%",
    }
    this.ref.detectChanges()
  }
  setListItemStyle() {
    let defaultStyle: ListItemStyle = new ListItemStyle({
      height: "fit-content",
      width: "100%",
      background: this.themeService.theme.palette.getBackground(),
      activeBackground: "transparent",
      borderRadius: "0",
      titleFont: fontHelper(this.themeService.theme.typography.title2),
      titleColor: this.themeService.theme.palette.getAccent(),
      border: "none",
      separatorColor: this.themeService.theme.palette.getAccent200(),
      hoverBackground: "transparent"
    })
    this.listItemStyle = { ...defaultStyle, ...this.listItemStyle }
  }
  setMessageInfoStyle() {
    let defaultStyle: MessageInformationStyle = new MessageInformationStyle({
      background: this.themeService.theme.palette.getBackground(),
      border: `1px solid ${this.themeService.theme.palette.getAccent50()}`,
      titleTextFont: fontHelper(this.themeService.theme.typography.title1),
      titleTextColor: this.themeService.theme.palette.getAccent(),
      emptyStateTextFont: fontHelper(this.themeService.theme.typography.title1),
      emptyStateTextColor: this.themeService.theme.palette.getAccent600(),
      errorStateTextFont: fontHelper(this.themeService.theme.typography.title1),
      errorStateTextColor: this.themeService.theme.palette.getAccent600(),
      loadingIconTint: this.themeService.theme.palette.getAccent600(),
      borderRadius: "8px",
      subtitleTextColor: this.themeService.theme.palette.getAccent600(),
      subtitleTextFont: fontHelper(this.themeService.theme.typography.caption2),
      dividerTint: this.themeService.theme.palette.getAccent100(),
      height: "500px",
      width: "500px",
      captionTextColor: this.themeService.theme.palette.getAccent600(),
      captionTextFont: fontHelper(this.themeService.theme.typography.caption2)
    })
    this.messageInformationStyle = { ...defaultStyle, ...this.messageInformationStyle }
  }
  ngOnDestroy() {
    this.onMessagesDelivered.unsubscribe()
    this.onMessagesRead.unsubscribe();
  }
  addMessageEventListeners() {
    try {
      this.onMessagesDelivered = CometChatMessageEvents.onMessagesDelivered.subscribe((messageReceipt: CometChat.MessageReceipt) => {
        if (this.message?.getId() == Number(messageReceipt.getMessageId())) {
          this.updateReceipt(messageReceipt)
          this.message?.setDeliveredAt(messageReceipt.getDeliveredAt())
          this.ref.detectChanges()
        }
      })
      this.onMessagesRead = CometChatMessageEvents.onMessagesRead.subscribe((messageReceipt: CometChat.MessageReceipt) => {
        if (this.message?.getId() == Number(messageReceipt.getMessageId())) {
          this.updateReceipt(messageReceipt)
          this.message?.setReadAt(messageReceipt.getReadAt())
          this.ref.detectChanges()
        }
      })
    } catch (error: any) {
      if (this.onError) {
        this.onError(CometChatException(error));
      }
    }
  }
  getAvatarUrl() {
    return this.isUserType ? (this.message.getReceiver() as CometChat.User).getAvatar() : ""
  }
  getUserReceipt() {
    return this.message.getReadAt() || this.message.getDeliveredAt()
  }
  getMessageReceipt() {
    let receipt = MessageReceiptUtils.getReceiptStatus(this.message)
    return receipt
  }
  updateReceipt(receipt: CometChat.MessageReceipt) {
    let messageKey = this.receipts.findIndex(
      (m: CometChat.MessageReceipt) => m.getSender().getUid() === receipt.getSender().getUid()
    );
    if (messageKey > -1) {
      if (receipt.getReceiptType() == receipt.RECEIPT_TYPE.READ_RECEIPT) {
        this.receipts[messageKey].setReadAt(receipt.getReadAt())
      }
      else {
        this.receipts[messageKey].setDeliveredAt(receipt.getDeliveredAt())
      }

    }

    this.ref.detectChanges();
  }
  getMessageReceipts() {
    if (this.message.getReceiverType() == CometChatUIKitConstants.MessageReceiverType.user) {
      this.isUserType = true;
      this.emptyStateText = localize("NO_RECIPIENT")
      this.states = States.loaded
      this.ref.detectChanges()
    }
    else {
      this.emptyStateText = localize("NO_RECIPIENTS")
      this.states = States.loading
      this.isUserType = false;
      CometChat.getMessageReceipts(this.message.getId()).then((receipts: any) => {
        this.receipts = receipts.filter((receipt: CometChat.MessageReceipt) => receipt.getSender().getUid() !== this.loggedInUser?.getUid()) as CometChat.MessageReceipt[];
        this.states = this.receipts.length > 0 ? States.loaded : States.empty;
        this.ref.detectChanges();
      })
        .catch((err: CometChat.CometChatException) => {
          this.states = States.error
        })
    }

  }
  closeClicked() {
    if (this.onClose) {
      this.onClose()
    }
  }
  closeButtonStyle = () => {
    return {
      height: "24px",
      width: "24px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint: this.themeService.theme.palette.getPrimary()
    }
  }
  getSubtitleStyle() {
    return {
      textFont: this.messageInformationStyle.subtitleTextFont,
      textColor: this.messageInformationStyle.subtitleTextColor
    }
  }
  getTitleStyle() {
    return {
      textFont: this.messageInformationStyle.titleTextFont,
      textColor: this.messageInformationStyle.titleTextColor
    }
  }
  wrapperStyle = () => {
    return {
      height: this.messageInformationStyle.height,
      width: this.messageInformationStyle.width,
      background: this.messageInformationStyle.background,
      border: this.messageInformationStyle.border,
      borderRadius: this.messageInformationStyle.borderRadius
    }
  }
  getCaptionStyle = () => {
    return {
      textFont: this.messageInformationStyle.captionTextFont,
      textColor: this.messageInformationStyle.captionTextColor
    }
  }

}
