  sendSticker = (event: any) => {
    this.stickerButtonRef?.nativeElement?.click();
    this.showStickerKeyboard = false;
    let sticker = event?.detail?.stickerURL;
    let stickerName: string = event?.detail?.stickerName;
    if (this.stickerConfiguration?.configuration?.ccStickerClicked) {
      this.stickerConfiguration?.configuration?.ccStickerClicked(
        {
          name: stickerName,
          url: sticker,
        },
        this.loggedInUser!,
        this.user,
        this.group,
        this.parentMessageId,
        this.onError,
        this.customSoundForMessage || this.customSoundForMessages,
        this.disableSoundForMessages
      );
    }
  };
  /**
   * @param  {any} event
   */
  onVideoChange(event: any): boolean {
    try {
      if (!event.target.files[0]) {
        return false;
      }
      const uploadedFile = event.target.files[0];
      const reader: any = new FileReader();
      reader.addEventListener(
        "load",
        () => {
          const newFile = new File(
            [reader.result],
            uploadedFile.name,
            uploadedFile
          );
          this.sendMediaMessage(
            newFile,
            CometChatUIKitConstants.MessageTypes.video
          );
        },
        false
      );
      reader.readAsArrayBuffer(uploadedFile);
    } catch (error: any) {
      if (this.onError) {
        this.onError(CometChatException(error));
      }
    }
    return true;
  }
