import { Component, OnInit, Input, ChangeDetectorRef, ChangeDetectionStrategy, TemplateRef, ViewChild, AfterViewInit } from "@angular/core";
import { CometChat } from "@cometchat/chat-sdk-javascript";
import { BaseStyle, GroupsConfiguration, UsersConfiguration, TabItemStyle, ContactsStyle } from '@cometchat/uikit-shared';
import '@cometchat/uikit-elements'
import { fontHelper, localize, CometChatTabItem, TabsVisibility, SelectionMode } from '@cometchat/uikit-resources'
import { CometChatThemeService } from "../../CometChatTheme.service";
/**
*
* CometChatContactsComponent is used to render group members to add
*
* @version 1.0.0
* @author CometChatTeam
* @copyright  2022 CometChat Inc.
*
*/
@Component({
  selector: "cometchat-contacts",
  templateUrl: "./cometchat-contacts.component.html",
  styleUrls: ["./cometchat-contacts.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CometChatContactsComponent implements OnInit, AfterViewInit {
  @ViewChild("usersRef") usersRef!: TemplateRef<any>;
  @ViewChild("groupsRef") groupsRef!: TemplateRef<any>;
  @Input() title: string = localize("NEW_CHAT");
  @Input() usersTabTitle: string = localize("USERS");
  @Input() groupsTabTitle: string = localize("GROUPS");
  @Input() usersConfiguration: UsersConfiguration = new UsersConfiguration({});
  @Input() groupsConfiguration: GroupsConfiguration = new GroupsConfiguration({});
  @Input() onSubmitButtonClick!: (users?: CometChat.User[], groups?: CometChat.Group[]) => void;
  @Input() closeIconURL: string = "assets/close2x.svg"
  @Input() contactsStyle: ContactsStyle = {};
  @Input() selectionMode: SelectionMode = SelectionMode.none;
  @Input() onClose!: () => void;
  @Input() onItemClick!: (user?: CometChat.User, group?: CometChat.Group) => void;
  @Input() tabVisibility: TabsVisibility = TabsVisibility.usersAndGroups
  @Input() selectionLimit: number = 5;
  @Input() tabs: CometChatTabItem[] = []
  @Input() hideSubmitButton: boolean = true;
  @Input() submitButtonText: string = "Submit";
  selection: typeof SelectionMode = SelectionMode;
  // public properties
  public usersRequestBuilder: CometChat.UsersRequestBuilder = new CometChat.UsersRequestBuilder().setLimit(30).hideBlockedUsers(true);
  public usersSearchRequestBuilder: CometChat.UsersRequestBuilder = new CometChat.UsersRequestBuilder().setLimit(30).hideBlockedUsers(true);
  public groupsRequestBuilder: CometChat.GroupsRequestBuilder = new CometChat.GroupsRequestBuilder().setLimit(30).joinedOnly(true);
  public groupsSearchRequestBuilder: CometChat.GroupsRequestBuilder = new CometChat.GroupsRequestBuilder().setLimit(30).joinedOnly(true);
  constructor(private ref: ChangeDetectorRef, private themeService: CometChatThemeService) {
  }
  public tabItemStyle: TabItemStyle = {};
  public usersList: CometChat.User[] = [];
  public groupsList: CometChat.Group[] = [];
  public submitButtonStyle: any = {
    height: "100%",
    width: "100%",
    background: "rgb(51, 153, 255)",
    padding: "8px",
    buttonTextColor: "white",
    buttonTextFont: "",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    border: "none",
    borderRadius: "8px"
  }
  public closeButtonStyle: any;
  public wrapperStyle: any;
  public contactsPadding: any;
  public titleStyle: any = {};
  public errorStyle: any = {};
  public isLimitReached: boolean = false;
  public tabsStyle: BaseStyle = {};
  onGroupSelected = (group: CometChat.Group) => {
    var key = this.groupsList.findIndex((m: any) => m?.getGuid() === group.getGuid());
    if (key >= 0) {
      this.groupsList.splice(key, 1);
    } else {
      this.groupsList.push(group);
    }
    this.isLimitReached = this.groupsList.length + this.usersList.length > this.selectionLimit;
    this.ref.detectChanges();
  };
  onUserSelected = (user: CometChat.User) => {
    const key = this.usersList.findIndex((m: any) => m?.getUid() === user.getUid());
    if (key >= 0) {
      this.usersList.splice(key, 1);
    } else {
      this.usersList.push(user);
    }
    this.isLimitReached = this.groupsList.length + this.usersList.length > this.selectionLimit;
    this.ref.detectChanges();
  };
  ngOnInit(): void {

    this.setcontactsStyle();
    this.usersList = []
    this.groupsList = []
  }
  ngAfterViewInit() {
    if (this.tabs?.length <= 0) {
      if (this.tabVisibility == TabsVisibility.usersAndGroups) {
        this.tabs = [
          {
            childView: this.usersRef,
            title: this.usersTabTitle,
            id: "users",
            style: this.tabItemStyle
          },
          {
            childView: this.groupsRef,
            title: this.groupsTabTitle,
            id: "groups",
            style: this.tabItemStyle
          }]
      }
      else {
        if (this.tabVisibility == TabsVisibility.users) {
          this.tabs = [
            {
              childView: this.usersRef,
              title: this.usersTabTitle,
              id: "users",
              style: this.tabItemStyle
            }]
        }
        else {
          this.tabs = [
            {
              childView: this.groupsRef,
              title: this.groupsTabTitle,
              id: "groups",
              style: this.tabItemStyle
            }]
        }
      }
      this.ref.detectChanges()
    }
  }
  userClicked = (user: CometChat.User) => {
    if (this.onItemClick) {
      this.onItemClick(user)
    }
  }
  groupClicked = (group: CometChat.Group) => {
    if (this.onItemClick) {
      this.onItemClick(undefined, group)
    }
  }
  closeClicked() {
    if (this.onClose) {
      this.onClose()
    }
  }
  submitClicked() {
    if (this.onSubmitButtonClick) {
      this.onSubmitButtonClick(this.usersList, this.groupsList)
    }
  }
  setcontactsStyle() {
    let defaultStyle: ContactsStyle = new ContactsStyle({
      background:  this.themeService.theme.palette.getBackground(),
      border: `none`,
      titleTextFont: fontHelper(this.themeService.theme.typography.title1),
      titleTextColor: this.themeService.theme.palette.getAccent(),
      errorStateTextColor: this.themeService.theme.palette.getError(),
      errorStateTextFont: fontHelper(this.themeService.theme.typography.title2),
      width: "100%",
      height: "100%",
      borderRadius: "none",
      closeIconTint: this.themeService.theme.palette.getPrimary(),
      submitButtonBackground: this.themeService.theme.palette.getPrimary(),
      submitButtonTextColor: this.themeService.theme.palette.getAccent900("light"),
      submitButtonTextFont: fontHelper(this.themeService.theme.typography.subtitle1),
      padding: "0 100px",
      tabBackground: "transparent",
      tabTitleTextFont: fontHelper(this.themeService.theme.typography.text2),
      tabTitleTextColor: this.themeService.theme.palette.getAccent(),
      activeTabTitleTextFont: fontHelper(this.themeService.theme.typography.text2),
      activeTabTitleTextColor: this.themeService.theme.palette.getAccent("light"),
      activeTabBackground: this.themeService.theme.palette.getAccent900("light"),
      activeTabBorder: "none",
      tabHeight: "fit-content",
      tabWidth: "100%"
    })
    this.contactsStyle = { ...defaultStyle, ...this.contactsStyle }
    this.submitButtonStyle.background = this.contactsStyle.submitButtonBackground;
    this.submitButtonStyle.buttonTextFont = this.contactsStyle.submitButtonTextFont;
    this.submitButtonStyle.buttonTextColor = this.contactsStyle.submitButtonTextColor;
    this.tabsStyle = {
      background: this.themeService.theme.palette.getAccent100(),
      borderRadius: "8px",
      border: `1px solid ${this.themeService.theme.palette.getAccent50()}`
    }
    this.tabItemStyle = new TabItemStyle({
      height: this.contactsStyle.tabHeight,
      width: this.contactsStyle.tabWidth,
      background: this.contactsStyle.tabBackground,
      activeBackground: this.contactsStyle.activeTabBackground,
      titleTextColor: this.contactsStyle.tabTitleTextColor,
      titleTextFont: this.contactsStyle.tabTitleTextFont,
      activeIconTint: this.themeService.theme.palette.getPrimary(),
      activeTitleTextColor: this.contactsStyle.activeTabTitleTextColor,
      activeTitleTextFont: this.contactsStyle.activeTabTitleTextFont,
      borderRadius: "8px"
    })
    this.closeButtonStyle = {
      height: "24px",
      width: "24px",
      border: "none",
      borderRadius: "0",
      background: "transparent",
      buttonIconTint: this.contactsStyle.closeIconTint || this.themeService.theme.palette.getPrimary()
    }
    this.wrapperStyle = {
      height: this.contactsStyle.height,
      width: this.contactsStyle.width,
      background: this.contactsStyle.background,
      border: this.contactsStyle.border,
      borderRadius: this.contactsStyle.borderRadius
    }
    this.contactsPadding = {
      padding: this.contactsStyle.padding
    }
    this.titleStyle = {
      textFont: this.contactsStyle.titleTextFont,
      textColor: this.contactsStyle.titleTextColor
    }
    this.errorStyle = {
      textFont: this.contactsStyle.errorStateTextFont,
      textColor: this.contactsStyle.errorStateTextColor
    }
  }
}
