import { AvatarStyle, BaseStyle, ListItemStyle } from '@cometchat/uikit-elements'
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnChanges, OnInit, SimpleChanges, TemplateRef } from '@angular/core';
import { CometChatGroupEvents, CometChatMessageEvents, CometChatTheme, CometChatUIKitConstants, CometChatUserEvents, IGroupLeft, IGroupMemberAdded, IGroupMemberJoined, IGroupMemberKickedBanned, IOwnershipChanged, fontHelper, localize } from '@cometchat/uikit-resources';

import { CometChat } from '@cometchat/chat-sdk-javascript';
import { CometChatException } from '../../Shared/Utils/ComeChatException';
import { CometChatThemeService } from '../../CometChatTheme.service';
import { MessageHeaderStyle } from '@cometchat/uikit-shared';
import { Subscription } from 'rxjs';
import { MessageUtils } from '../../Shared/Utils/MessageUtils';

/**
*
* CometChatMessageHeader is a used to render listitem component.
*
* @version 1.0.0
* @author CometChatTeam
* @copyright  2022 CometChat Inc.
*
*/
@Component({
  selector: 'cometchat-message-header',
  templateUrl: './cometchat-message-header.component.html',
  styleUrls: ['./cometchat-message-header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CometChatMessageHeaderComponent implements OnInit, OnChanges {
  @Input() avatarStyle: AvatarStyle = {
    borderRadius: "16px",
    width: "28px",
    height: "28px",
    border: "none",

  }
  @Input() statusIndicatorStyle: BaseStyle = {
    borderRadius: "16px",
    width: "10px",
    height: "10px",
    border: "none",
  }
  @Input() messageHeaderStyle: MessageHeaderStyle = {
    width: "100%",
    height: "100%",
  }
  @Input() listItemStyle: ListItemStyle = {
    width: "",
    height: "100%",
    border: "none",
    borderRadius: "2px",
    separatorColor: "",
    activeBackground: "transparent",
    hoverBackground: "transparent"
  }
  @Input() subtitleView: any;
  @Input() disableUsersPresence: boolean = false;
  @Input() disableTyping: boolean = false;
  /**
 * @deprecated
 *
 * This property is deprecated as of version 4.3.7 due to newer property 'passwordGroupIcon'. It will be removed in subsequent versions.
 */
  @Input() protectedGroupIcon: string = "assets/Locked.svg";
  @Input() passwordGroupIcon: string | undefined = undefined;
  @Input() privateGroupIcon: string = "assets/Private.svg";
  @Input() menu: any;
  @Input() user!: CometChat.User;
  @Input() group!: CometChat.Group;
  @Input() backButtonIconURL: string = "assets/backbutton.svg";
  @Input() hideBackButton: boolean = false;
  @Input() listItemView!: TemplateRef<any>;
  @Input() onError: ((error: CometChat.CometChatException) => void) | null = (error: CometChat.CometChatException) => {
    console.log(error)
  }
  @Input() onBack: () => void = () => { }
  public groupsListenerId: string = "groupsList_" + new Date().getTime();
  userListenerId = "userlist_" + new Date().getTime();
  public subtitleText: string = "";
  public loggedInUser!: CometChat.User;
  public isTyping: boolean = false;
  theme: CometChatTheme = new CometChatTheme({})
  ccGroupMemberAdded!: Subscription;
  ccGroupLeft!: Subscription;
  ccGroupMemberJoined!: Subscription;
  ccGroupMemberKicked!: Subscription;
  ccGroupMemberBanned!: Subscription;
  ccOwnershipChanged!: Subscription;
  onTypingStarted!: Subscription;
  ccUserBlocked!: Subscription;
  ccUserUnblocked!: Subscription;
  onTypingEnded!: Subscription;
  constructor(private ref: ChangeDetectorRef, private themeService: CometChatThemeService) {
  }
  ngOnChanges(changes: SimpleChanges): void {
    if (changes["user"] || changes["group"]) {
      this.removeListener()
      if (!this.loggedInUser) {
        CometChat.getLoggedinUser().then((user: CometChat.User | null) => {
          this.loggedInUser = user as CometChat.User;
          this.attachListeners()
          this.updateSubtitle()
        }).catch((error: CometChat.CometChatException) => {
          if (this.onError) {
            this.onError(error)
          }
        })
      }
      else {
        this.attachListeners()
        this.updateSubtitle()
      }

    }
  }
  ngOnInit(): void {
    this.setListItemStyle()
    this.setAvatarStyle()
    this.setStatusStyle()
    this.subscribeToEvents();
    this.backButtonStyle.buttonIconTint = this.messageHeaderStyle?.backButtonIconTint || this.themeService.theme.palette.getPrimary();
    this.statusColor.online = this.messageHeaderStyle.onlineStatusColor ?? this.themeService.theme.palette.getSuccess()

  }
  // subscribe to global events
  subscribeToEvents() {
    this.ccGroupMemberAdded = CometChatGroupEvents.ccGroupMemberAdded.subscribe((item: IGroupMemberAdded) => {
      if (this.group && this.group.getGuid() == item?.userAddedIn!.getGuid()) {
        this.group == item?.userAddedIn;
        this.ref.detectChanges()
        this.updateSubtitle()
      }
    })
    this.ccGroupMemberBanned = CometChatGroupEvents.ccGroupMemberBanned.subscribe((item: IGroupMemberKickedBanned) => {
      if (this.group && this.group.getGuid() == item?.kickedFrom!.getGuid()) {
        this.group == item?.kickedFrom;
        this.ref.detectChanges()
        this.updateSubtitle()
      }
    })
    this.ccGroupMemberJoined = CometChatGroupEvents.ccGroupMemberJoined.subscribe((item: IGroupMemberJoined) => {
      if (this.group && this.group.getGuid() == item?.joinedGroup!.getGuid()) {
        this.group == item?.joinedGroup;
        this.ref.detectChanges()
        this.updateSubtitle()
      }
    })
    this.ccGroupMemberKicked = CometChatGroupEvents.ccGroupMemberKicked.subscribe((item: IGroupMemberKickedBanned) => {
      if (this.group && this.group.getGuid() == item?.kickedFrom!.getGuid()) {
        this.group == item?.kickedFrom;
        this.ref.detectChanges()
        this.updateSubtitle()
      }
    })
    this.ccOwnershipChanged = CometChatGroupEvents.ccOwnershipChanged.subscribe((item: IOwnershipChanged) => {
      if (this.group && this.group.getGuid() == item?.group!.getGuid()) {
        this.group == item?.group;
        this.ref.detectChanges()
        this.updateSubtitle();
      }
    })
    this.ccGroupLeft = CometChatGroupEvents.ccGroupLeft.subscribe((item: IGroupLeft) => {
      if (this.group && this.group.getGuid() == item?.leftGroup!.getGuid() && this.loggedInUser?.getUid() == item?.userLeft?.getUid()) {
        this.group == item?.leftGroup;
        this.ref.detectChanges()
        this.updateSubtitle()
      }
    })
  }
  // unsubscribe to subscribed events.
  unsubscribeToEvents() {
    this.ccGroupMemberAdded?.unsubscribe();
    this.ccGroupMemberBanned?.unsubscribe();
    this.ccGroupMemberJoined?.unsubscribe();
    this.ccGroupMemberKicked?.unsubscribe();
    this.ccOwnershipChanged?.unsubscribe();
    this.ccUserBlocked?.unsubscribe();
    this.ccUserUnblocked?.unsubscribe();
    this.ccGroupLeft?.unsubscribe();
  }
  setListItemStyle() {
    let defaultStyle: ListItemStyle = new ListItemStyle({
      height: "45px",
      width: "100%",
      background: this.themeService.theme.palette.getBackground(),
      activeBackground: "transparent",
      borderRadius: "0",
      titleFont: fontHelper(this.themeService.theme.typography.title2),
      titleColor: this.themeService.theme.palette.getAccent(),
      border: "none",
      separatorColor: "",
      hoverBackground: "transparent"
    })
    this.listItemStyle = { ...defaultStyle, ...this.listItemStyle }
  }
  setAvatarStyle() {
    let defaultStyle: AvatarStyle = new AvatarStyle({
      borderRadius: "24px",
      width: "36px",
      height: "36px",
      border: "none",
      backgroundColor: this.themeService.theme.palette.getAccent700(),
      nameTextColor: this.themeService.theme.palette.getAccent900(),
      backgroundSize: "cover",
      nameTextFont: fontHelper(this.themeService.theme.typography.subtitle1),

      outerViewBorderSpacing: "",
    })
    this.avatarStyle = { ...defaultStyle, ...this.avatarStyle }
  }
  setStatusStyle() {
    let defaultStyle: BaseStyle = {
      height: "12px",
      width: "12px",
      border: "none",
      borderRadius: "24px",

    }
    if(!this.disableUsersPresence){
    this.statusIndicatorStyle = { ...defaultStyle, ...this.statusIndicatorStyle }
    }else {
      this.statusIndicatorStyle = {};
    }
  }
  /**
   * @param  {CometChat.User} user
   */
  getStatusIndicatorStyle = (user: CometChat.User) => {
    let userStatusVisibility = new MessageUtils().getUserStatusVisibility(user) || this.disableUsersPresence;
    if(!userStatusVisibility){
      return(
        this.statusIndicatorStyle
      )
    }
    return null;
  }
  getHeadersStyle(): MessageHeaderStyle {
    const defaultValues = this.messageHeaderStyle;
    let defaultStyle: MessageHeaderStyle = new MessageHeaderStyle({
      background: defaultValues.background || this.themeService.theme.palette.getBackground(),
      border: defaultValues.border || `none`,
      onlineStatusColor: defaultValues.onlineStatusColor || this.themeService.theme.palette.getSuccess(),
      privateGroupIconBackground: defaultValues.privateGroupIconBackground || this.themeService.theme.palette.getSuccess(),
      passwordGroupIconBackground: defaultValues.passwordGroupIconBackground || "RGB(247, 165, 0)",
      backButtonIconTint: defaultValues.backButtonIconTint || this.themeService.theme.palette.getPrimary(),
      subtitleTextColor: defaultValues.subtitleTextColor || this.themeService.theme.palette.getAccent600(),
      subtitleTextFont: defaultValues.subtitleTextFont || fontHelper(this.themeService.theme.typography.subtitle2),
      typingIndicatorTextColor: defaultValues.typingIndicatorTextColor || this.themeService.theme.palette.getPrimary(),
      typingIndicatorTextFont: defaultValues.typingIndicatorTextFont || fontHelper(this.themeService.theme.typography.subtitle1),
      height: defaultValues.height || "45px",
      width: defaultValues.width || "100%",
    });

    return defaultStyle;
  }
  public statusColor: any = {
    online: "#00f300",
    private: "#00f300",
    password: "#F7A500",
    public: ""
  }
  backButtonStyle: any = {
    height: "24px",
    width: "24px",
    border: "none",
    borderRadius: "none",
    background: "transparent",
    buttonIconTint: ""
  }
  checkStatusType = () => {
    if (this.user) {
      let userStatusVisibility = new MessageUtils().getUserStatusVisibility(this.user) || this.disableUsersPresence;
      return userStatusVisibility ? null : this.statusColor[this.user?.getStatus()];
    }
    else if (this.group) {
      return this.statusColor[this.group?.getType()];
    }
    else return;
  }
  onBackClicked() {
    if (this.onBack) {
      this.onBack()
    }
  }
  updateSubtitle() {
    const count = this.group?.getMembersCount();
    const membersText = localize(count > 1 ? "MEMBERS" : "MEMBER");
    if (this.user) {
      let userStatusVisibility = this.user.getBlockedByMe() || this.user.getHasBlockedMe() || this.disableUsersPresence;
      this.subtitleText = userStatusVisibility ? "" : this.user.getStatus();
      this.ref.detectChanges();
    }
    else {
      this.subtitleText = `${count} ${membersText}`;
      this.ref.detectChanges();
    }

  }
  getSubtitleView() {
    return this.subtitleView(this.user || this.group);
  }
  checkGroupType(): string {
    let image: string = "";
    if (this.group) {
      switch (this.group?.getType()) {
        case CometChatUIKitConstants.GroupTypes.password:
          image = this.passwordGroupIcon || this.protectedGroupIcon;
          break;
        case CometChatUIKitConstants.GroupTypes.private:
          image = this.privateGroupIcon;
          break;
        default:
          image = ""
          break;
      }
    }
    return image
  }
  updateUserStatus(user: CometChat.User) {
    if (this.user && this.user.getUid() && this.user.getUid() === user.getUid()) {
      this.user.setStatus(user.getStatus());
      this.updateSubtitle()
    }
    // this.ref.detectChanges();
  }

  handleGroupEvent = (group: CometChat.Group, user: CometChat.User, hasJoined: boolean, newScope?: CometChat.GroupMemberScope) => {
    if (this.group.getGuid() !== group.getGuid()) {
      return;
    }
    if (user.getUid() === this.loggedInUser?.getUid()) {
      group.setHasJoined(hasJoined);
      if (newScope) {
        group.setScope(newScope);
      }
    }
    this.group = group;
    this.ref.detectChanges();
    this.updateSubtitle();
  };
  

  attachListeners() {
    try {
      CometChat.addUserListener(
        this.userListenerId,
        new CometChat.UserListener({
          onUserOnline: (onlineUser: CometChat.User) => {
            /* when someuser/friend comes online, user will be received here */
            this.updateUserStatus(onlineUser);
          },
          onUserOffline: (offlineUser: CometChat.User) => {
            /* when someuser/friend went offline, user will be received here */
            this.updateUserStatus(offlineUser);
          },
        })
      );
      if (this.user) {
        this.ccUserBlocked = CometChatUserEvents.ccUserBlocked.subscribe(
          (item: CometChat.User) => {
            if (item.getUid() == this.user.getUid())
              this.updateUserStatus(item);
          }
        );
        this.ccUserUnblocked = CometChatUserEvents.ccUserUnblocked.subscribe(
          (item: CometChat.User) => {
            if (item.getUid() == this.user.getUid())
              this.updateUserStatus(item);
          }
        );
      }

      if (!this.disableTyping) {
        this.onTypingStarted = CometChatMessageEvents.onTypingStarted.subscribe((typingIndicator: CometChat.TypingIndicator) => {
          this.isTyping = true;
          this.setTypingIndicatorText(typingIndicator)
        });
        this.onTypingEnded = CometChatMessageEvents.onTypingEnded.subscribe((typingIndicator: CometChat.TypingIndicator) => {
          this.isTyping = false;
          this.updateSubtitle()
        });
      }
      CometChat.addGroupListener(
        this.groupsListenerId,
        new CometChat.GroupListener({
          onGroupMemberScopeChanged: (message: CometChat.Action, changedUser: CometChat.User, newScope: CometChat.GroupMemberScope, oldScope: CometChat.GroupMemberScope, changedGroup: CometChat.Group) => {
            this.handleGroupEvent(changedGroup, changedUser, true, newScope);
          },
          onGroupMemberKicked: (message: CometChat.Action, kickedUser: CometChat.User, kickedBy: CometChat.User, kickedFrom: CometChat.Group) => {
            this.handleGroupEvent(kickedFrom, kickedUser, false);
          },
          onMemberAddedToGroup: (message: CometChat.Action, userAdded: CometChat.User, userAddedBy: CometChat.User, userAddedIn: CometChat.Group) => {
            this.handleGroupEvent(userAddedIn, userAdded, true);
          },
          onGroupMemberLeft: (message: CometChat.Action, leavingUser: CometChat.User, group: CometChat.Group) => {
            this.handleGroupEvent(group, leavingUser, false);
          },
          onGroupMemberJoined: (message: CometChat.Action, joinedUser: CometChat.User, joinedGroup: CometChat.Group) => {
            this.handleGroupEvent(joinedGroup, joinedUser, true);
          }
        })
      );
    } catch (error: any) {
      if (this.onError) {
        this.onError(CometChatException(error))
      }
    }
  }
  removeListener() {
    CometChat.removeUserListener(this.userListenerId);
    this.onTypingStarted?.unsubscribe();
    this.onTypingEnded?.unsubscribe();
  }
  ngOnDestroy() {
    this.removeListener()
    this.unsubscribeToEvents()
  }
  setTypingIndicatorText = (typing: CometChat.TypingIndicator) => {
    const sender = typing.getSender();
    const receiverId = typing.getReceiverId();
    const loggedInUser = this.loggedInUser;

    if (this.user && sender.getUid() === this.user?.getUid() && loggedInUser?.getUid() === receiverId) {
      const isBlocked = this.user.getBlockedByMe() || this.user.getHasBlockedMe();
      if (isBlocked) {
        return;
      }

      this.subtitleText = localize("IS_TYPING");
      this.ref.detectChanges();
    } else if (this.group && this.group.getGuid() === receiverId) {
      this.subtitleText = `${sender.getName()} ${localize("IS_TYPING")}`;
      this.ref.detectChanges();
    }
  }

  headerStyle = () => {
    const headerStyle = this.getHeadersStyle();
    return {
      width: headerStyle.width,
      height: headerStyle.height,
      border: headerStyle.border,
      borderRadius: headerStyle.borderRadius,
      background: headerStyle.background,
    }
  }
  subtitleStyle = () => {
    const headerStyle = this.getHeadersStyle();

    if (this.user && this.user.getStatus() == CometChatUIKitConstants.userStatusType.online) {
      return {
        textFont: headerStyle.subtitleTextFont,
        textColor: this.themeService.theme.palette.getPrimary()
      }
    }
    else {
      return {
        textFont: this.isTyping ? headerStyle.typingIndicatorTextFont : headerStyle.subtitleTextFont,
        textColor: this.isTyping ? headerStyle.typingIndicatorTextColor : headerStyle.subtitleTextColor
      }
    }
  }
}
